.container {
  max-width: 120rem;
  height: 100%;
  padding: 3rem 6.5rem 1rem 4.5rem;
  background: rgba(4, 20, 35, 0.85);
}
.on__back:focus {
  color: #e53935;
}
.description {
  margin: 3rem 0 0 0;
}
.description__review {
  margin: 2.2rem 0;
}
.description__row {
  display: flex;
  align-items: flex-start;
}
.description__col {
  margin: 0 2rem 0 0;
}
.description__text {
  height: 10rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-wrap: wrap;
}
.description__text span {
  margin: 0 1rem 0 0;
}
.description__title {
  color: #FF0000;
}
