.container {
  max-width: 120rem;
  height: 100%;
  padding: 3rem 7rem 0 7rem;
  background: rgba(4, 20, 35, 0.8);
}
.preview__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.preview__img {
  width: 20.6rem;
}
.description {
  padding: 0 0 0 1rem;
  margin: 3rem 0 2rem 0;
  border-left: #FF0000 solid 0.2rem;
  line-height: normal;
}
.on__back:focus {
  color: #FF0000;
}
.title {
  width: fit-content;
}
.button__group {
  display: flex;
  align-items: center;
}
.rating {
  display: flex;
  margin: 4rem 0 2rem 1.2rem;
}
.rating__kp {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 2rem 0 0;
}
.rating__imbd {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.review {
  height: 5.7rem;
  padding: 0 1rem 0 0;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 1rem 0 1rem 0;
}
.like.active {
  color: #0D9F00;
}
.deslike.active {
  color: #FF0000;
}
.bookmark.active {
  color: #FF0000;
}
