.kitten {
  padding: 24px;
}
.kitten:focus {
  background-color: #e6e6e6;
  color: black;
  border: 1px solid tomato;
  transform: scale(1.1);
  opacity: 1;
}
.title__mainList {
  font-size: 2rem;
  font-weight: 600;
  padding: 0 3rem;
  margin: 0 0 0 5.5rem;
}
.on__title__focus {
  padding: 0 3rem;
  margin: 0 0 0 5.5rem;
  width: 100%;
}
.on__title__focus a {
  font-size: 2rem;
  font-weight: 600;
}
.on__title__focus:focus a {
  color: #FF0000;
}
.container {
  display: flex;
  line-height: normal;
  background: #041423;
  color: #FFFFFF;
  height: 100vh;
}
.container a {
  color: azure;
}
.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 89vw;
  height: 19rem;
  overflow: hidden;
  padding: 0 1rem 0 0;
  margin: 0 0 0 8rem;
  flex-shrink: 0;
}
.plug {
  width: 1rem;
  padding: 0.5rem;
}
.list__item {
  margin: 0 0.3rem;
  font-size: 1rem;
  border-radius: 0.1rem;
}
.list__item:focus {
  border: 0.1rem solid #FF0000;
  transform: scale(1.05);
  border-radius: 0.2rem;
}
.item__last {
  font-weight: bold;
  text-align: center;
  padding: 5.5rem 0;
  width: 10rem;
  height: 16rem;
  font-size: 2rem;
  line-height: 2.5rem;
  color: #FFFFFF;
  background: rgba(255, 255, 255, 0.25);
  border-radius: 0.2rem;
}
.list__item .item__cover {
  width: 10rem;
  height: 16rem;
  border-radius: 0.2rem;
}
.list__item .item__cover img {
  width: 100%;
  height: 100%;
  border-radius: 0.2rem;
}
.item__details {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 1rem;
  padding: 0 0 0 0.5rem;
}
.nowrap {
  white-space: nowrap;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.block__column {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 1rem 0;
  overflow: hidden;
  position: relative;
}
.arrow__up,
.arrow__down {
  position: absolute;
  text-align: center;
  width: 88vw;
  left: 12%;
}
.arrow__up svg,
.arrow__down svg {
  color: rgba(255, 255, 255, 0.55);
  width: 3.5rem;
  height: 3.5rem;
}
.arrow__up {
  top: 0;
  transform: rotate(180deg);
}
.arrow__down {
  bottom: 12%;
}
.arrow__down:focus svg,
.arrow__up:focus svg {
  color: #FF0000;
}
.main__list_focus {
  padding: 0 0 0 8rem;
  flex-grow: 1;
}
.focus__description {
  font-size: 1.2rem;
  line-height: normal;
}
.focus__title {
  font-size: 1.8rem;
  line-height: normal;
}
.focus__genre {
  margin: 0.5rem 0;
  color: #B7B7B7;
  font-size: 1rem;
  line-height: 1.2rem;
}
.focus__content {
  height: 4.3rem;
  padding: 0 1rem 0 0;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0 0 1rem 0;
}
.focus__rate_age {
  color: #FFFFFF;
  font-size: 0.9rem;
  font-weight: bold;
  border: #FFFFFF solid 0.1rem;
  border-radius: 0.45rem;
  padding: 0.3rem 0.4rem;
  margin: 0 0 0 0.5rem;
}
.focus__rating {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0.5rem 0 1rem 0;
}
.focus__rating__kp {
  display: flex;
  flex-direction: column;
}
.focus__rate__number {
  text-align: center;
}
.focus__rating__imbd {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 0 0 1rem;
}
.focus__small {
  font-size: 0.8rem;
}
