.player {
  width: 100%;
  height: 100%;
  background-color: #000000;
  position: relative;
}
.player-wrapper {
  position: relative;
  padding-top: 56.25% /* Player ratio: 100 / (1280 / 720) */;
}
.react-player {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
}
.on__back {
  position: absolute;
  top: 2rem;
  left: 10rem;
  z-index: 1000;
}
.on__back:focus {
  color: #FF0000;
}
.title_controls {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
}
.controls {
  position: absolute;
  bottom: 0.5rem;
  left: 2rem;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.input_control {
  width: 75vw;
  margin: 0 1rem;
}
.time_controls {
  min-width: 5.5rem;
}
.btn_group {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}
.btn_controls {
  color: #FFFFFF;
}
.btn_controls svg {
  border-radius: 50%;
  width: 4.2rem;
  height: 4.2rem;
}
.btn_controls_arrow svg {
  width: 3.2rem;
  height: 3.2rem;
}
.p_controls {
  padding: 0.2rem 0 0.2rem 0.2rem;
}
.btn_controls:focus svg {
  background-color: #FF0000;
}
